let ordered = collection => {
  if (
    collection.some(note => {
      return note.order
    })
  ) {
    collection = collection.sort((a, b) => {
      let valA = a.order ? a.order : 0
      let valB = b.order ? b.order : 0
      return valA - valB
    })
    collection = collection.reverse()
  }
  return collection
}
export default ordered



